@import url('https://fonts.googleapis.com/css2?family=Boldonse&display=swap');




.App {
  text-align: center;
  background-color: #161616;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header {
  color: #fff;
  height: 8vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header a {
  margin-left: 5%;
  color: #fff;
  text-decoration: none;
}

.userIcon{
  margin-right: 20px;
}

.HomeScreen {
  height: 80vh;
  width: 95vw;
  background-color: #2d2d2d;
  border-radius: 10px;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.first {
  display: flex;
  flex-direction: row;
  margin-top: 10%;
  justify-content: space-around;
}

.titles {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.first h1 {
  font-size: 1.5rem;
  width: 90%;
  font-family: '';
  text-align: left;
  font-family: "Boldonse", system-ui;
  font-weight: 400;
  font-style: normal;
  /* margin-left: 50px; */
}

.second{
  background-color: #713f12;
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 20%;
  width: 90%;
  border-left: #a16207 5px solid;
  border-radius: 10px;
  font-size: 1rem;
  text-align: left;
  margin-top: 20%;
}

.second p{
  margin-left: 10px;
}

.first p {
  font-size: .5rem;
  width: 90%;
  font-family: '';
  text-align: start;
  font-style: normal;
  margin-left: 10px;
  margin-top: 20px;
}

.first img {
  display: none;
}

/* From Uiverse.io by mrhyddenn */
.downloadBtn {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  background: none;
  color: #0f1923;
  cursor: pointer;
  position: relative;
  padding: 8px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  transition: all .15s ease;
  margin-top: 20%;
  width: 80%;
}

.downloadBtn::before,
.downloadBtn::after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  height: calc(50% - 5px);
  border: 1px solid #7D8082;
  transition: all .15s ease;
}

.downloadBtn::before {
  top: 0;
  border-bottom-width: 0;
}

.downloadBtn::after {
  bottom: 0;
  border-top-width: 0;
}

.downloadBtn:active,
.downloadBtn:focus {
  outline: none;
}

.downloadBtn:active::before,
.downloadBtn:active::after {
  right: 3px;
  left: 3px;
}

.downloadBtn:active::before {
  top: 3px;
}

.downloadBtn:active::after {
  bottom: 3px;
}

.downloadBtn_lg {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: #fff;
  background-color: #161616;
  overflow: hidden;
  box-shadow: inset 0px 0px 0px 1px transparent;
}

.downloadBtn_lg::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 2px;
  background-color: #0f1923;
}

.downloadBtn_lg::after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 4px;
  height: 4px;
  background-color: #0f1923;
  transition: all .2s ease;
}

.downloadBtn_sl {
  display: block;
  position: absolute;
  top: 0;
  bottom: -1px;
  left: -8px;
  width: 0;
  background-color: #9fff46;
  transform: skew(-15deg);
  transition: all .2s ease;
}

.downloadBtn_text {
  position: relative;
  font-family: "Boldonse", system-ui;
  font-weight: 400;
  font-style: normal;
}

.downloadBtn:hover {
  color: #0f1923;
}

.downloadBtn:hover .downloadBtn_text {
  color: #0f1923;
}

.downloadBtn:hover .downloadBtn_sl {
  width: calc(100% + 15px);
}

.downloadBtn:hover .downloadBtn_lg::after {
  background-color: #fff;
}

/* Login screen */ 
.form-container {
  width: 90vw;
  height: 60vh;
  border-radius: 0.75rem;
  background-color: rgb(23, 33, 53);
  color: rgba(243, 244, 246, 1);
  padding-top: 30px;
}

.title {
  text-align: center;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
}

.form {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-group {
  margin-top: 0.25rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-group label {
  color: rgba(156, 163, 175, 1);
  margin-bottom: 4px;
  text-align: left;
  width: 75vw;
}

.input-group input {
  width: 75vw;
  border-radius: 0.375rem;
  border: 1px solid rgba(55, 65, 81, 1);
  outline: 0;
  background-color: rgb(23, 33, 53);
  padding: 0.75rem 1rem;
  color: rgba(243, 244, 246, 1);
  margin-bottom: 20px;
}

.input-group textarea {
  width: 75vw;
  border-radius: 0.375rem;
  border: 1px solid rgba(55, 65, 81, 1);
  outline: 0;
  background-color: rgb(23, 33, 53);
  padding: 0.75rem 1rem;
  color: rgba(243, 244, 246, 1);
  margin-bottom: 20px;
  resize: none;
}

.input-group .des {
  width: 75vw;
  height: 100px;
  border-radius: 0.375rem;
  border: 1px solid rgba(55, 65, 81, 1);
  outline: 0;
  background-color: rgb(23, 33, 53);
  padding: 0.75rem 1rem;
  color: rgba(243, 244, 246, 1);
  vertical-align: top;
}

.input-group input:focus {
  border-color: rgba(167, 139, 250);
}

.input-group textarea:focus {
  border-color: rgba(167, 139, 250);
}



.sign {
  display: block;
  width: 90%;
  background-color: #1DB954;
  padding: 0.75rem;
  text-align: center;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 0.375rem;
  font-weight: 600;
  height: 50px;
  margin-top: 30px;
  max-width: 600px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.signup {
  text-align: center;
  font-size: 0.75rem;
  line-height: 1rem;
  color: rgba(156, 163, 175, 1);
}


